<template>
	<view>
		<!-- 自定义导航栏 -->
		<qs-nav-bar>
			<!-- 导航栏中间部分 -->
			<view style="margin-left: 144rpx;">
				<swiper-tab :current="tabIndex" :items="tabbars" :scrollFlag="true" :isEqually="false" @clickItem="handleTabTap"></swiper-tab>
			</view>
		</qs-nav-bar>

		<!-- 内容区域 -->
		<view class="main-box animate__animated animate__fadeInUp">
			<swiper :style="{ height: swiperHeight + 'px' }" :current="tabIndex" @change="handleSwiperChange">
				
				<!-- 附近 -->
				<swiper-item>
					<scroll-view v-if="nearList.length" scroll-y class="list" @scrolltolower="handleLoadMore()" enable-back-to-top>
						<!-- 图文列表 -->
						<!-- <dynamic-box v-for="(item, index1) in items.data" :key="index1" :item="item" :index="index1"></dynamic-box> -->
						<dynamic-box></dynamic-box>
						<!-- 上拉加载 -->
						<uni-load-more :status="status" :content-text="contentText" v-if="items.data.length > 0" @clickLoadMore="handleLoadMore" />
						<!-- 没有数据 -->
						<view v-else style="margin-top: 200rpx;"> <no-data></no-data> </view>
					</scroll-view>
				</swiper-item>
				
				<!-- 关注 -->
				<swiper-item v-if="tabList.length" v-for="(items, index) in tabList" :key="index">
					<scroll-view v-if="items.data.length" scroll-y class="list" @scrolltolower="handleLoadMore()" enable-back-to-top>
						<!-- 图文列表 -->
						<topic-box v-for="(item, index1) in items.data" :key="index1" :item="item" :index="index1"></topic-box>
						<!-- 上拉加载 -->
						<uni-load-more :status="status" :content-text="contentText" v-if="items.data.length > 0" @clickLoadMore="handleLoadMore" />
						<!-- 没有数据 -->
						<view v-else style="margin-top: 200rpx;"> <no-data></no-data> </view>
					</scroll-view>
				</swiper-item>
				
				<!-- 话题 -->
				<swiper-item v-if="tabList.length" v-for="(items, index) in tabList" :key="index">
					<scroll-view v-if="items.data.length" scroll-y class="list" @scrolltolower="handleLoadMore()" enable-back-to-top>
						<!-- 图文列表 -->
						<topic-box v-for="(item, index1) in items.data" :key="index1" :item="item" :index="index1"></topic-box>
						<!-- 上拉加载 -->
						<uni-load-more :status="status" :content-text="contentText" v-if="items.data.length > 0" @clickLoadMore="handleLoadMore" />
						<!-- 没有数据 -->
						<view v-else style="margin-top: 200rpx;"> <no-data></no-data> </view>
					</scroll-view>
				</swiper-item>
				
			</swiper>
		</view>
	</view>
</template>

<script>
import qsNavBar from '../../components/custom/qs-nav-bar/qs-nav-bar.vue';
import dynamicBox from '../../components/custom/dynamic/dynamic-box.vue';
import swiperTab from '../../components/custom/swiper-tab/swiper-tab.vue';
import noData from '../../components/custom/no-data/no-data.vue';
export default {
	components: {
		swiperTab,
		qsNavBar
	},
	data() {
		return {
			status: 'more', // 上拉加载 [more|loading|noMore] loading 的状态
			contentText: {
				contentdown: '上拉加载更多~',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的~'
			},
			nearList:[],  // 附近列表
			followList:[],  // 关注列表
			huatiList:[],  // 话题列表
			tabList: [], // 数据列表
			swiperHeight: 500,
			tabIndex: 1,
			tabbars: [
				{
					id: 1,
					name: '附近'
				},
				{
					id: 2,
					name: '关注'
				},
				{
					id: 3,
					name: '话题'
				}
			]
		};
	},
	/* 生命周期函数 */
	onLoad() {
		// #ifdef MP
		//获取胶囊按钮的数据,微信小程序
		this.topMenu = uni.getMenuButtonBoundingClientRect();
		// #endif

		/* 获取话题分类 */
		// this.getTabbars();
	},
	onReady() {
		// 这里的scroll-view设置占满整个屏幕，height设置100%；swiper的height设置成屏幕的高度-减去tab栏的高度(50)-原生导航栏的高度(44),需要微信提供的api获取设备屏幕高度数据
		let res = uni.getSystemInfoSync();
		// #ifdef APP-PLUS
		this.swiperHeight = res.windowHeight - 144;
		// #endif

		// #ifndef APP-PLUS
		this.swiperHeight = res.windowHeight - 94;
		// #endif
	},
	methods: {
		/* 导航栏点击事件 */
		handleTabTap(e) {
			this.tabIndex = e.currentIndex;
		},
		/* 页签切换事件 */
		handleSwiperChange(e) {
			let index = e.detail.current;
			if (this.tabIndex === index) {
				return;
			}
			this.tabIndex = index;
		},

		/* 上拉加载 TODO;修改status文字没变 */
		handleLoadMore(e) {
			this.status = 'loading';
			let data = [];

			setTimeout(() => {
				/* 模拟数据 */
				this.tabList[this.tabIndex].data.push(...data);
				// this.getTopicList();
			}, 500);
			this.status = 'more';
		}
	}
};
</script>

<style lang="scss" scoped></style>
